<script lang="ts">
  function closeNavBar() {
    document.getElementById("navBar").style.display = "none";
    document.getElementById("dashboard").style.marginLeft = "0";
    window.dispatchEvent(new Event("resize")); // Snap plotly graphs to new width
  }

  let widths = [];
  for (let i = 0; i < 4; i++) {
    widths.push(35 + Math.floor(Math.random() * 8) * 5);
  }

  export let current_page: string,
    pages: string[],
    switchPage: Function;
</script>

<nav id="navBar">
  <div class="title no-selection">
    <p>
      <span style="color: var(--green)">pl</span>dashboard
    </p>
    <div class="fantasy-logo">Fantasy</div>
  </div>
  <div class="team-links">
    {#if pages.length === 0}
      {#each widths as width, _}
        <div
          class="placeholder"
          style="width: {width}%"
        />
      {/each}
    {:else}
      {#each pages as _page, _ (_page)}
        {#if _page.toLowerCase() === current_page}
          <a href="/fantasy{_page === "All" ? '' : '/' + _page.toLowerCase()}" class="team-link">
            <div class="this-team-container">
              <div class="this-team-name">
                {_page}
              </div>
            </div>
          </a>
        {:else}
          <button
            class="team-link"
            on:click={() => {
              switchPage(_page.toLowerCase());
            }}
          >
            <div class="team-container">
              <div class="team-name">
                {_page}
              </div>
            </div>
          </button>
        {/if}
      {/each}
    {/if}
  </div>
  <div class="close">
    <button class="close-btn" on:click={closeNavBar}>
      <img src="img/arrow-bar-left.svg" alt="" />
    </button>
  </div>
</nav>

<style scoped>
  .title {
    color: white;
    font-size: 1.6em;
    height: 96px;
    display: grid;
    place-items: center;
  }
  .fantasy-logo {
    color: white;
    position: absolute;
    font-size: 0.67em;
    top: 59px;
    right: 40px;
  }
  .no-selection {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
  }
  .team-links {
    font-size: 1em;
    color: var(--pink);
    display: grid;
  }
  button {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    text-align: left;
  }
  .team-name,
  .this-team-name {
    padding: 0.4em 1.4em;
  }

  .this-team-container {
    color: var(--purple);
    background: var(--green);
  }

  :hover.team-container {
    background: #2c002f;
    background: #140921;
  }
  nav {
    position: fixed;
    width: 220px;
    height: 100vh;
    background: #37003c;
    background: var(--purple);
  }
  img {
    height: 25px;
    width: 25px;
  }
  .close-btn {
    position: absolute;
    right: 0.9em;
    bottom: 0.6em;
    background: transparent;
    border: none;
    outline: none;
    padding-top: 0.3em;
    cursor: pointer;
  }

  .placeholder {
    height: 19px;
    margin: 6px 21px;
    width: 40px;
    background: #c600d8;
    border-radius: 4px;
    opacity: 0.25;
    position: relative;
    overflow: hidden;
  }

  .placeholder::before {
    content: "";
    display: block;
    position: absolute;
    left: -100px;
    top: 0;
    height: 100%;
    width: 150px;
    background: linear-gradient(
      to right,
      transparent 0%,
      #e8e8e8 50%,
      transparent 100%
    );
    background: linear-gradient(
      to right,
      transparent 0%,
      #eea7f4 50%,
      transparent 100%
    );
    animation: load 1s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  }
  @keyframes load {
    from {
      left: -100px;
    }
    to {
      left: 100px;
    }
  }

  @media only screen and (max-width: 1200px) {
    #navBar {
      display: none;
    }
  }
</style>
