<script lang="ts">
  import { onMount } from "svelte";

function getColours(position: string) {
    switch (position) {
        case "Forward":
            return "#c600d8"
        case "Midfielder":
            return "#00fe87"
        case "Defender":
            return "#2dbaff"
        case "Goalkeeper":
            return "#280936"
    }
}

  function lines(data: any, page: string): any[] {
    let teams = []
    let points = []
    let price = []
    let minutes = []
    let colours = []
    let maxMinutes = 0
    for (let team of Object.keys(data)) {
        if (team != "_id") {
            teams.push(team)
            points.push(data[team].points === null ? 0 : data[team].points)
            price.push(data[team].price == null ? 0 : data[team].price/10)
            minutes.push(data[team].minutes == null ? 0 : data[team].minutes / 2) 
            if (minutes[minutes.length-1] > maxMinutes) {
                maxMinutes = minutes[minutes.length-1]
            }
            colours.push(getColours(data[team].position))      
          }
    }

    let playtimes = minutes.map((x) => ((x/maxMinutes) * 100).toFixed(1))

    let markers = {
      x: points,
      y: price,
      name: "test",
      mode: "markers",
      type: "scatter",
      marker: {
        size: minutes,
        colorscale: [
          [0, "#00fe87"],
          [0.5, "#f3f3f3"],
          [1, "#f83027"],
        ],
        opacity: 0.75,
        color: colours,
      },
      customdata: playtimes,
      text: teams,
      hovertemplate: `<b>%{text}</b><br><b>£%{y}m</b><br><b>%{x} points</b><br>%{customdata}% playtime<extra></extra>`,
      showlegend: false,
    };

    // Add this team last to ensure it overlaps all other lines
    return [markers]
  }

  function defaultLayout() {
    return {
      title: false,
      autosize: true,
      margin: { r: 20, l: 60, t: 0, b: 40, pad: 5 },
      hovermode: "closest",
      plot_bgcolor: "transparent",
      paper_bgcolor: "transparent",
      height: 700,
      yaxis: {
        title: { text: "Price" },
        gridcolor: "gray",
        showgrid: false,
        showline: false,
        zeroline: false,
        fixedrange: true,
        visible: true,
      },
      xaxis: {
        title: { text: "Points" },
        linecolor: "black",
        showgrid: false,
        showline: false,
        fixedrange: true,
      },
      dragmode: false,
    };
  }

  function setDefaultLayout() {
    if (setup) {
      let layoutUpdate = {
        "yaxis.title": { text: "Position" },
        "yaxis.visible": true,
        "yaxis.tickvals": Array.from(Array(20), (_, i) => i + 1),
        "margin.l": 60,
        "margin.t": 15,
      };
      //@ts-ignore
      Plotly.update(plotDiv, {}, layoutUpdate, 0);
    }
  }

  function setMobileLayout() {
    if (setup) {
      let layoutUpdate = {
        "yaxis.title": null,
        "yaxis.visible": false,
        "yaxis.tickvals": Array.from(Array(10), (_, i) => i + 2),
        "margin.l": 20,
        "margin.t": 5,
      };

      let sizes = plotData.data[0].marker.size;
      console.log
      for (let i = 0; i < sizes.length; i++) {
        sizes[i] = Math.round(sizes[i] / 2);
      }
      let dataUpdate = {
        marker: {
          size: sizes,
          color: plotData.data[0].marker.color,
          opacity: 0.75,
        },
      };

      plotData.data[0].marker.size = sizes;

      //@ts-ignore
      Plotly.update(plotDiv, dataUpdate, layoutUpdate, 0);
    }
  }

  function buildPlotData(data: any, team: string): PlotData {
    let plotData = {
      data: lines(data, team),
      layout: defaultLayout(),
      config: {
        responsive: true,
        showSendToCloud: false,
        displayModeBar: false,
      },
    };
    return plotData;
  }

  let plotDiv: HTMLDivElement, plotData: PlotData;
  let setup = false;
  onMount(() => {
    genPlot();
    setup = true;
  });

  function genPlot() {
    plotData = buildPlotData(data, page);
    //@ts-ignore
    new Plotly.newPlot(
      plotDiv,
      plotData.data,
      plotData.layout,
      plotData.config
    ).then((plot) => {
      // Once plot generated, add resizable attribute to it to shorten height for mobile view
      plot.children[0].children[0].classList.add("resizable-graph")
      plot.children[0].children[0].classList.add("tall-graph");
    });
  }

  function refreshPlot() {
    if (setup) {
      let newPlotData = buildPlotData(data, page);
     plotData.data[0] = newPlotData.data[0];

      //@ts-ignore
      Plotly.redraw(plotDiv);
      if (mobileView) {
        setMobileLayout();
      }
    }
  }

  $: page && refreshPlot();
  $: !mobileView && setDefaultLayout();
  $: setup && mobileView && setMobileLayout();

  export let data: any, page: string, mobileView: boolean;
</script>

<div id="plotly">
  <div id="plotDiv" bind:this={plotDiv}>
    <!-- Plotly chart will be drawn inside this DIV -->
  </div>
</div>

